const images = document.querySelectorAll(".img-skill");
const choosen = document.querySelector("#img-choosen");

let before = [-30, 10, -30, 0, 0, -30];

function actualizarPosicion(mouseX, mouseY) {
  let difX = (mouseX - screen.width / 2) / 50;
  let difY = (mouseY - screen.height / 2) / 50;

  for (let i = 0; i < images.length; i++) {
    images[i].style.transform = `rotateZ(${before[i]}deg) translate3d(${
      difX * -1
    }px ,${difY * -1}px , 0)`;
  }

  choosen.style.transform = `rotateZ(${before[0]}deg) translate3d(${
    difX * -1
  }px ,${difY * -1}px , 0)`;
}

// Función para obtener la posición del mouse
function obtenerPosicion(e) {
  console.log(e);
  setTimeout(() => {
    actualizarPosicion(e.clientX, e.clientY);
  }, 200);
}

// Agregar un evento de escucha para el movimiento del mouse
document.addEventListener("mousemove", obtenerPosicion);
